<template>
  <div class="md-dialog-container">
    <md-dialog-title style="opacity: 0.4;font-family: Arial;font-size: 14px;color:#32353e;">ACCOUNT SETTINGS</md-dialog-title>

    <!--<md-tabs md-dynamic-height>
       <md-tab md-label="Account Settings">-->
    <md-dialog-content>
        <AccountSettings :siteId="siteId" :section="section" :env="env" />
    </md-dialog-content>
    <!--</md-tab>

    <md-tab md-label="Static Window" md-disabled>

    </md-tab>
    </md-tabs>-->

    <md-dialog-actions style="justify-content: space-around">
      <md-button class="md-primary cancel-button" @click="toggleDialog">Cancel</md-button>
      <md-button class="md-primary apply-button" @click="saveAccountSettings">Apply</md-button>
    </md-dialog-actions>
  </div>
</template>

<script>
import AccountSettings from '@/components/AccountSettings'
export default {
  name: 'DialogMenu',
  props: ['showDialog', 'section', 'siteId', 'env'],
  data: () => ({
  }),
  components: {
    AccountSettings
  },
  mounted () {
  },
  methods: {
    toggleDialog () {
      // console.log('toggleDialog triggered')
      this.$root.$emit('toggleDialog')
    },
    saveAccountSettings () {
      // console.log('toggleDialog triggered')
      this.$root.$emit('saveAccountSettings')
      this.$root.$emit('toggleDialog')
    }
  }
}
</script>

<style lang="less" scoped>
  .md-dialog {
    max-width: 768px;
  }
  .apply-button {
  }
  .cancel-button {
  }
</style>
<style lang="less">
  .md-radio.md-theme-default.md-checked {
    .md-radio-container {
      border-color: #5879da !important;
    }
    .md-radio-container:after {
      background-color: #5879da !important;
    }
  }
  .apply-button {
    width: 115px !important;
    height: 40px !important;
    border-radius: 28px !important;
    background-color: #fa7921 !important;
    .md-button-content {
      font-family: Roboto;
      font-size: 14px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: center;
      text-transform: none;
      color: #ffffff;
    }
  }
  .cancel-button {
    width: 103px !important;
    height: 40px !important;
    border-radius: 28px !important;
    background-color: transparent !important;
    .md-button-content {
      font-family: Roboto;
      font-size: 14px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: center;
      text-transform: none;
      color: #65686e;
    }
  }
</style>



