// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import { MdCard, MdDivider, MdButton, MdToolbar, MdContent, MdDrawer, MdApp, MdIcon, MdList, MdField, MdMenu, MdDialog, MdTabs, MdRadio, MdDialogConfirm } from 'vue-material/dist/components'
import 'vue-material/dist/vue-material.min.css'
import 'vue-material/dist/theme/default.css'
import vueHeadful from 'vue-headful'
import VueSimpleSVG from 'vue-simple-svg'

Vue.use(VueSimpleSVG)
Vue.component('vue-headful', vueHeadful)

Vue.use(MdToolbar)
Vue.use(MdContent)
Vue.use(MdDrawer)
Vue.use(MdApp)
Vue.use(MdIcon)
Vue.use(MdList)
Vue.use(MdButton)
Vue.use(MdDivider)
Vue.use(MdList)
Vue.use(MdCard)
Vue.use(MdField)
Vue.use(MdMenu)
Vue.use(MdDialog)
Vue.use(MdTabs)
Vue.use(MdRadio)
Vue.use(MdDialogConfirm)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



