<template>
  <div style="width:100%;height:100%">
    <div class="md-layout" style="height:60px;border-bottom:1px">
      <div class="md-toolbar-section-end">
        <md-button id="refresh-button" class="refresh-button" @click="frame.src += ''" :disabled="disable">
          <svg xmlns="http://www.w3.org/2000/svg" width="22" height="22" svg-inline="" class="icon" role="presentation" focusable="false" tabindex="-1"><g fill="none" fill-rule="evenodd"><path d="M0 0h22v22H0z"/><path fill="#FA7921" d="M10.5 2.75A8.25 8.25 0 0118.75 11l-.002-.096.815-.813a.75.75 0 01.976-.073l.084.072a.75.75 0 010 1.06l-2.121 2.124a.75.75 0 01-1.061 0l-2.12-2.123a.75.75 0 011.062-1.06l.867.868a6.75 6.75 0 10-1.629 4.438.75.75 0 111.138.978A8.25 8.25 0 1110.5 2.75z"/></g></svg>
          <span>
            Refresh
          </span>
        </md-button>
      </div>
    </div>
    <div style="height:calc(100% - 60px)">
      <iframe id="testArea" @load="onMyFrameLoad" style="width:100%;height:100%;border:none;" :src="updatedFrame">
      </iframe>
    </div>
  </div>
</template>

<script>
import cssExamples from '../assets/json/css_examples'
const styleId = 'styles'
export default {
  name: 'WindowArea',
  props: ['section', 'siteId', 'env'],
  mounted () {
    // this.loadWithTag(iframe)
  },
  data () {
    return {
      frame: null,
      css: cssExamples.example1.css,
      initSrc: './static/demo.html',
      highlightStyles: 'outline: 2px dashed #fa7921 !important; box-shadow: 0 0 0 99999px rgba(0, 0, 0, .4) !important; fill: #fa7921 !important;',
      highlightStylesBody: 'border: 2px dashed #fa7921 !important; box-shadow: 0 0 0 99999px rgba(0, 0, 0, .4) !important; fill: #fa7921 !important;',
      currentElements: []
    }
  },
  computed: {
    updatedFrame () {
      return this.updateSrc()
    },
    disable () {
      return !this.siteId
    }
  },
  methods: {
    loadWithStyle (frame, styles) {
      let frameDocument = frame.contentWindow.document
      if (!frameDocument.getElementById(styleId)) {
        let styleObject = frameDocument.createElement('style')
        styleObject.id = styleId
        styleObject.innerHTML = styles
        frameDocument.body.appendChild(styleObject)
      } else {
        frameDocument.getElementById(styleId).innerHTML = styles
      }
    },
    updateHighlightCss (frame, selector) {
      let frameDocument = frame.contentWindow.document
      try {
        this.currentElements = frameDocument.querySelectorAll(selector)
      } catch (err) {
        console.log(err)
      }
      if (this.currentElements && this.currentElements.length > 0) {
        this.currentElements.forEach(function (element) {
          let highlightStyle = this.highlightStyles
          if (selector.indexOf('lpc_body') !== -1) {
            highlightStyle = this.highlightStylesBody
          }
          if (!element.getAttribute('data-original-style')) {
            element.setAttribute('data-original-style', element.style.cssText || 'none')
            element.style.cssText += highlightStyle
          }
        }.bind(this))
      }
    },
    resetHighlightCss () {
      if (this.currentElements && this.currentElements.length > 0) {
        this.currentElements.forEach(function (element) {
          let origStyle = element.getAttribute('data-original-style')
          if (origStyle) {
            element.style.cssText = origStyle === 'none' ? '' : origStyle
            element.removeAttribute('data-original-style')
          }
        })
        this.currentElements = []
      }
    },
    resetHighlightCssAll (frame) {
      let frameDocument = frame.contentWindow.document
      let modifiedElements = frameDocument.querySelectorAll('[data-original-style]')
      if (modifiedElements && modifiedElements.length > 0) {
        this.currentElements.forEach(function (element) {
          element.style.cssText = element.getAttribute('data-original-style')
          element.removeAttribute('data-original-style')
        })
      }
    },
    onMyFrameLoad () {
      // let frame = document.getElementById('testArea')
      this.frame = document.getElementById('testArea')
      this.loadWithStyle(this.frame, this.css)
      this.$root.$on('updateIframe', data => {
        // console.log(data)
        this.loadWithStyle(this.frame, data)
        this.css = data
      })
      this.$root.$on('reloadIframe', () => {
        this.frame.src += ''
      })
      this.$root.$on('updateHighlightCss', function (selector) {
        this.updateHighlightCss(this.frame, selector)
      }.bind(this))
      this.$root.$on('resetHighlightCss', function (selector) {
        this.resetHighlightCss(this.frame, selector)
      }.bind(this))
      this.$root.$on('resetHighlightCssAll', function () {
        this.resetHighlightCssAll(this.frame)
      }.bind(this))
    },
    updateSrc () {
      return this.initSrc + `?siteId=${this.siteId}&section=${this.section}&env=${this.env}`
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.refresh-button {
  svg, span {
    vertical-align: middle;
  }
  right: 15px;
  border-radius: 24.5px;
  border: solid 2px #fa7921 !important;
  font-family: Arial;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-transform: none;
  color: #fa7921;
}
</style>
<style lang="less">
  .refresh-button#refresh-button {
    border-radius: 24.5px;
    border: solid 2px #fa7921;
    font-family: Arial;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #fa7921;
    &:disabled {
      opacity: 0.4;
    }
  }
</style>



