<template>
  <div class="page-container">
    <md-app class="mainApp">
      <md-app-toolbar class="md-primary top-bar">
        <TopBar :siteId="siteId"/>
      </md-app-toolbar>
      <md-app-content style="padding: 0px 0 0px 0;">
        <md-dialog class="dialogMenu" :md-backdrop=true :md-active.sync="showDialog" :md-click-outside-to-close=true>
          <DialogMenu :siteId="siteId" :section="section" :env="env" />
        </md-dialog>
        <div class="md-layout" style="height:100%">
          <div class="md-layout-item md-size-40">
            <Editor></Editor>
          </div>
          <div class="md-layout-item md-size-60">
            <WindowArea :siteId="siteId" :section="section" :env="env"/>
          </div>
        </div>
      </md-app-content>
    </md-app>
  </div>
</template>

<script>
import Editor from '@/components/Editor'
import TopBar from '@/components/TopBar'
import WindowArea from '@/components/WindowArea'
import DialogMenu from '@/components/DialogMenu'

export default {
  name: 'Main',
  data () {
    return {
      siteId: this.$route.query.siteId || '',
      section: this.$route.query.section || '',
      env: this.$route.query.env || 'ga',
      showDialog: !this.$route.query.siteId
    }
  },
  components: {
    Editor,
    TopBar,
    WindowArea,
    DialogMenu
  },
  mounted () {
    // this.siteId = this.$route.query.siteId || this.siteId
    // this.section = this.$route.query.section || this.section
    // this.env = this.$route.query.env || this.env
    this.$root.$on('toggleDialog', () => {
      console.log('toggleDialog received')
      this.showDialog = !this.showDialog
    })
    this.$root.$on('updateFrameSettings', (settings) => {
      this.siteId = settings.siteId
      this.section = settings.section
      this.env = settings.env
      this.addParamsToLocation({
        siteId: this.siteId,
        section: this.section,
        env: this.env
      })
    })
    // this.loadWithTag(iframe)
  },
  methods: {
    addParamsToLocation (params) {
      history.pushState(
        {},
        null,
        this.$route.path +
        '?' +
        Object.keys(params)
          .map(key => {
            return (
              encodeURIComponent(key) + '=' + encodeURIComponent(params[key])
            )
          })
          .join('&')
      )
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style language="less" scoped>
.mainApp {
  min-height: 100vh;
}
.sideBar {
  background-color: black;
  width: 350px;
}
.tool-bar {
  //background-color: #edecec !important;
  padding-right: 0px !important;
  min-height: 50px;
}
.top-bar {
  background-color: #5879da !important;
}
.dialogMenu {
  left: calc(1% + 140px);
  top: 270px;
  border-radius: 10px;
}
</style>
<style>
  svg {
    margin: 5px;
  }
  .md-overlay.md-fixed.md-dialog-overlay {
    background: rgba(255,255,255,.6);
  }
  .md-dialog-actions {
    padding-bottom: 25px !important;
  }
</style>



