<template>
  <div>
    <md-field>
      <label>Account Id</label>
      <md-input v-model="accountId"></md-input>
    </md-field>
    <md-field>
      <label>Section</label>
      <md-input v-model="sect"></md-input>
    </md-field>
    <md-radio v-model="environment" :value="envOptions.GA">GA</md-radio>
    <md-radio v-model="environment" :value="envOptions.ALPHA">Alpha</md-radio>
    <md-radio v-model="environment" :value="envOptions.QA">QA</md-radio>
  </div>
</template>
<script>
export default {
  name: 'AccountSettings',
  props: {
    siteId: String,
    env: {
      type: String,
      default: 'ga'
    },
    section: {
      type: String,
      default: ''
    }
  },
  /* computed: {
    accountId: this.siteId,
    environment: this.env,
    sect: this.section
  }, */
  data: function () {
    return {
      envOptions: {
        GA: 'ga',
        ALPHA: 'alpha',
        QA: 'qa'
      },
      accountId: this.siteId,
      environment: this.env,
      sect: this.section
    }
  },
  mounted () {
    this.$root.$on('saveAccountSettings', () => {
      this.$root.$emit('updateFrameSettings', {
        siteId: this.accountId,
        env: this.environment,
        section: this.sect
      })
    })
  },
  methods: {
    toggleDialog () {
      // console.log('toggleDialog triggered')
      this.$root.$emit('toggleDialog')
    }
  }
}
</script>

<style lang="less" scoped>

</style>



